import React, { Component } from 'react';
import './index.scss';
import { connect } from 'react-redux';
import { fromJS, is } from 'immutable';

class Xcolor extends Component {
	componentDidMount() {
	}

	shouldComponentUpdate(nextProps, nextState) {
		return !is(fromJS(this.props), fromJS(nextProps)) || !is(fromJS(this.state), fromJS(nextState))
	}
	
	render() {
		const { defaultOptions } = this.props.configs
		return (
			<div id="x-color">
				<h3>Color 色彩</h3>
				<p className="mt-20 mb-20">为了避免视觉传达差异，使用一套特定的调色板来规定颜色，为你所搭建的产品提供一致的外观视觉感受。</p>
				<div className="brand border-radius4 mb-20">主色 #409EFF</div>
				<p>详见element ui</p>
			</div>
		)
	}
}

export default connect(state => ({
	configs: state.configs
}), {

})(Xcolor)
